import { ClipBase } from './base';
import * as THREE from 'three';
import { LottieClip } from '../../common/types/clip';
import { watch } from '../../common/utils/watch';
import { AnimationItem } from 'lottie-web';
import { loadLottie } from '../../common/utils';

export class Lottie extends ClipBase<LottieClip> {
  private geometry?: THREE.PlaneGeometry;
  private material?: THREE.MeshBasicMaterial;
  private lottieInstance?: AnimationItem;
  private container: HTMLDivElement = document.createElement('div');

  public async init() {
    document.body.appendChild(this.container);
    await this.initLottie();
    this.initRender();
    this.watchChange();
  }

  private watchChange() {
    watch(this.config, 'zIndex', (val: number) => {
      if (this.sprite) {
        this.sprite.position.z = val;
      }
    });
  }

  private async initLottie() {
    this.lottieInstance = await loadLottie(this.container, this.config.animationData);
  }

  private initRender() {
    const canvas = this.container.querySelector('canvas'); // this.lottieInstance.container????
    if (this.lottieInstance && canvas) {
      const { player: { width, height } } = this;
      this.geometry = new THREE.PlaneGeometry(width, height);
      this.material = new THREE.MeshBasicMaterial();
      const texture = new THREE.CanvasTexture(canvas);
      this.material.map = texture;
      this.material.transparent = true;
      this.sprite = new THREE.Mesh(this.geometry, this.material);
    }
  }

  public async onUpdate() {
    if (this.material?.map && this.lottieInstance) {
      const { player: { currentTime } } = this;
      this.lottieInstance.goToAndStop(currentTime - this.config.positionInTimeline.start);
      this.material.map.needsUpdate = true;
    }
  }

  public async destroy() {
    super.destroy();
    this.lottieInstance?.destroy();
    this.geometry?.dispose();
    this.material?.dispose();
  }
}